var app = {};
app.$root = $('html, body');

app.windowSize = function () {
  var e = window,
      a = 'inner';

  if (!('innerWidth' in window)) {
    a = 'client';
    e = document.documentElement || document.body;
  }

  return {
    width: e[a + 'Width'],
    height: e[a + 'Height']
  };
}; // scroll to top button


var _scrollHelper = function _scrollHelper() {
  var scrollTopBtnFlag = true;
  var $scrollTopBtn = $('.scroll-top-btn');
  $scrollTopBtn.click(function () {
    app.$root.animate({
      scrollTop: 0
    }, 1000);
    return false;
  });
  $(window).scroll(function () {
    // reveal scroll top button
    if ($(this).scrollTop() > 500) {
      if (scrollTopBtnFlag) {
        $scrollTopBtn.addClass('reveal-btn');
        scrollTopBtnFlag = false;
      }
    } else {
      if (!scrollTopBtnFlag) {
        $scrollTopBtn.removeClass('reveal-btn');
        scrollTopBtnFlag = true;
      }
    }

    if ($(window).scrollTop() + $(window).height() == $(document).height()) {
      $scrollTopBtn.removeClass('reveal-btn');
      scrollTopBtnFlag = true;
    }
  });
}; // quantity input


var _quantityInput = function _quantityInput() {
  $(document).on("click", ".qty-control", function () {
    var $input = $(this).parent().find(".qty-input");
    var max = parseInt($input.attr("max"));
    var min = parseInt($input.attr("min"));
    var operator = $(this).attr("data-action");
    var qty = parseInt($input.val());

    if (operator == "add") {
      qty += 1;

      if (qty >= min + 1) {
        $(this).siblings(".qty-minus").attr("disabled", false);
      }

      if (qty >= max) {
        $(this).attr("disabled", true);
      }
    } else {
      qty = qty <= min ? min : qty -= 1;

      if (qty == min) {
        $(this).attr("disabled", true);
      }

      if (qty < max) {
        $(this).siblings(".qty-add").attr("disabled", false);
      }
    }

    $input.val(qty);
  });
  $(document).on("change", ".qty-input", function () {
    var $minusBtn = $(this).siblings(".qty-minus");
    var $addBtn = $(this).siblings(".qty-add");
    var max = parseInt($(this).attr("max"));
    var min = parseInt($(this).attr("min"));
    var qty = parseInt($(this).val());

    if (isNaN(qty) || qty < min) {
      $(this).val(min);
      $minusBtn.attr("disabled", true);
    } else {
      $minusBtn.attr("disabled", false);

      if (qty >= max) {
        $(this).val(max);
        $addBtn.attr("disabled", true);
      } else {
        $(this).val(qty);
        $addBtn.attr("disabled", false);
      }
    }
  });
};

$(document).ready(function () {
  _scrollHelper();

  _quantityInput(); // plugins init


  AOS.init({
    once: true,
    duration: 500
  });
  cssVars();
  objectFitImages();
});